import React from 'react'
import PropTypes from 'prop-types'
import Grid from '@material-ui/core/Grid'
import ForecastItem from './../ForecastItem'
import { validValues } from './../IconState' 

const renderForcastItem = forecast => {

    const { weekDay, hour, state, temperature } = ForecastItem
    return (
        //Para listas se debe incorporar una key unica
        <Grid item key={`${weekDay}${hour}`}>
            <ForecastItem weekDay={weekDay} hour={hour} state={state} temperature={temperature} />
        </Grid>
    )

}

const Forecast = ({ forecastItemList }) => {
    return (
        <>
            <Grid container
                justify="center"
                alignItems="center">
                {
                    forecastItemList.map(forecast => renderForcastItem(forecast))
                }
            </Grid>
        </>
    )
}

//ForecastItemList es un array con una forma
Forecast.propTypes = {

    forecastItemList: PropTypes.arrayOf(PropTypes.shape({

        weekDay: PropTypes.string.isRequired,
        hour: PropTypes.number.isRequired,
        state: PropTypes.oneOf(validValues).isRequired,
        temperature: PropTypes.number.isRequired,

    })).isRequired,
    
        
    

}

export default Forecast
